/**
 * Entry for Marketo integrations.
 */

// const querystring = require('querystring');

window.dataLayer = window.dataLayer || [];

const mktoFormConfig = {
  podId: '//info.a16z.com',
  munchkinId: '382-JZB-798',
  formIds: [505, 577, 670, 676, 758, 763, 766, 769, 770, 771,
    772, 773, 774, 775, 777, 778, 780, 781, 820, 823, 824, 876, 879, 880, 881,
    1545, 1541, 1560, 1561, 1562, 1559, 1564, 1565, 1563, 1568, 1569, 1567,
    1572, 1573, 1571, 1600, 1601, 1599, 1633, 1859, 2112, 2113, 2131, 2037,
    2347, 2369, 2372, 2403, 2492, 2618, 2703, 2715, 2735, 2869, 2870, 2877],
  trackIds: [1633],
};

/**
 * Self-executing commands for Marketo forms integration.
 *
 * @param  object config   Configuration object.
 */
(function mktoFormChain(config) {
  const MKTOFORM_ID_ATTRNAME = 'data-formId';

  /**
   * Fix inter-form label bug.
   *
   * @param  object (form)   Form object.
   */
  MktoForms2.whenRendered((form) => {
    const formEl = form.getFormElem()[0];
    const rando = `_${new Date().getTime()}${Math.random()}`;

    Array.from(formEl.querySelectorAll('label[for]')).forEach((labelEl) => {
      const label = labelEl;
      const forEl = formEl.querySelector(`[id^="${labelEl.htmlFor}"]`);
      if (forEl) {
        // label.htmlFor = forEl.id = `${forEl.id}${rando}`;
        let randoParam = '';
        if (0 > forEl.id.indexOf(rando)) {
          randoParam = rando;
        }
        const elId = `${forEl.id}${randoParam}`;
        label.htmlFor = elId;
        forEl.id = elId;
      }
    });

    /**
     * Switch out the arrow icon used in the submit button.
     */
    const formSubmitBtns = formEl.querySelectorAll('.mktoButton');
    Array.from(formSubmitBtns).forEach((btn, index) => {
      const newButton = document.createElement('img');
      newButton.setAttribute('alt', '');
      newButton.setAttribute('id', `subscribeBtn-${index}`);
      newButton.setAttribute(
        'src',
        '/wp-content/themes/a16z-2015/client/images/arrow-right.svg'
      );
      newButton.classList.add('subscribe-input__btn');
      const submitBtn = btn;
      // Remove all children from the target node.
      if (submitBtn.firstChild) {
        submitBtn.removeChild(submitBtn.firstChild);
        // submitBtn.firstChild.remove();
      }

      // Add our new image instead.
      submitBtn.appendChild(newButton);

      // Add label to button.
      submitBtn.setAttribute(
        'aria-label',
        'Submit email and proceed to the next step'
      );
    });
  });

  /**
   * Open Marketo page after submission.
   *
   * @param  {object} (form) Marketo form element.
   */
  MktoForms2.whenReady((form) => {
    /**
     * Send Google Analytics event beacon.
     */
    form.onSubmit(() => {
      console.log('submit old');
      const formEl = form.getFormElem()[0];
      const formInstance = formEl.dataset.forminstance || 'default';
      if ('undefined' !== typeof ga) {
        // Create a GA snippet and send event.
        ga('create', 'UA-45022052-14');
        ga('send', {
          hitType: 'event',
          eventCategory: 'Subscriptions',
          eventAction: 'Subscription Started',
          eventLabel: `Form ID ${form.getId()} Instance ${formInstance}`,
        });
      }
      const data = {
        event: 'MarketoEvent',
        eventCategory: 'Form Submit',
        eventAction: 'Subscription Started',
        eventLabel: `Form ID ${form.getId()} Instance ${formInstance}`,
        eventValue: null,
      };
      // console.log(data);
      // eslint-disable-next-line no-undef
      dataLayer.push(data);
    });

    form.onSuccess(() => {
      // Get value of marketo token, if present.
      // eslint-disable-next-line no-restricted-globals
      /*
      const { mkt_tok } = querystring.parse(location.search.substring(1)) || {
        mkt_tok: '',
      };

      // Get value of email input
      let { value: email } = document.getElementById('Email') || {
        value: '',
      };

      // Parse Form Fields
      const formValues = form.getValues();

      if ('undefined' !== typeof formValues &&
        'undefined' !== typeof formValues.Email) {
        email = formValues.Email;
      }
      */
      console.log(form.getId());
      if (2372 === form.getId()
        || 2347 === form.getId()
        || 2369 === form.getId()) {
        console.log('scroll to top');
        window.scrollTo(0, 0);
      }
      // eslint-disable-next-line no-undef
      fbq('track', 'Lead', { content_category: 'Submit_Newsletter_Form' });
      const inPixel = document.createElement('img');
      inPixel.width = 1;
      inPixel.height = 1;
      inPixel.style = 'display:none;';
      // eslint-disable-next-line max-len
      inPixel.src = '//px.ads.linkedin.com/collect/?pid=2801140&conversionId=4534425&fmt=gif';

      const formEl = form.getFormElem();
      const mktoButton = formEl.find('.mktoButton[type="submit"]');
      if (mktoButton.length) {
        const submitBtn = mktoButton.get(0);
        const newButton = document.createElement('img');
        newButton.setAttribute('alt', '');
        newButton.setAttribute(
          'src',
          '/wp-content/themes/a16z-2015/client/images/arrow-right.svg'
        );
        newButton.classList.add('subscribe-input__btn');

        // Remove all children from the target node and remove disabled state.
        submitBtn.removeAttribute('disabled');
        mktoButton.text('');

        // Add our new image instead.
        submitBtn.appendChild(newButton);
      }
      let messageElContainer =
        formEl.parents().find(formEl.data('thankyouContainer'));
      const messageEl = formEl.data('thankyouMessage');
      if (0 === messageElContainer.length) {
        messageElContainer = formEl;
      }
      if ('undefined' !== typeof messageEl && jQuery(messageEl).length) {
        const messageHeight = messageElContainer.height();
        messageElContainer.css({
          display: 'none',
        });
        jQuery(messageEl)
          .removeAttr('style')
          .css({ height: messageHeight });
        return false;
      }
      /* // Open new window and navigate to new form page.
      const newWindow = window.open('');
      newWindow.document.location = `https://pages.a16z.com/Email?mkt_tok=${mkt_tok}`; // eslint-disable-line camelcase

      if (670 === form.getId()) {
        newWindow.document.location = `https://pages.a16z.com/EmailSummit?Email=${email}&mkt_tok=${mkt_tok}`; // eslint-disable-line camelcase
      } else {
        newWindow.document.location = `https://pages.a16z.com/Email?mkt_tok=${mkt_tok}`; // eslint-disable-line camelcase
      } */
      // Prevent the page from reloading.
      return false;
    });
  });

  /**
   * Ensure only one Marketo form exists at a time
   */
  Array.from(config.formIds).forEach((formId) => {
    const elements = Array.from(document.querySelectorAll(`
      [${MKTOFORM_ID_ATTRNAME}="${formId}"]
    `));
    if (0 < elements.length) {
      const loadForm = MktoForms2.loadForm.bind(
        MktoForms2,
        config.podId,
        config.munchkinId,
        formId
      );

      (function loadFormCb(formEls) {
        const formEl = formEls.shift();
        if (formEl) {
          formEl.id = `mktoForm_${formId}`;
        }

        loadForm(() => {
          if (formEl) {
            formEl.id = '';
          }

          if (formEls.length) {
            loadFormCb(formEls);
          }
        });
      }(elements));
    }
  });
}(mktoFormConfig));
